<script>
export default {
  name: 'UserIndex',
  data() {
    return {
      products: [],
      product: {},
      cart: {},
      coupon_code: '',
      status: {
        loadingItem: '', // Â∞çÊáâÂìÅÈ†Ö id
      },

    };
  },
  mounted() {
    console.log('‚úÖ UserIndex.vue Â∑≤ÊéõËºâÔºåÈñãÂßãËºâÂÖ• JS...');
    this.reloadAllScripts();
  },
  beforeRouteEnter(to, from, next) {
    next((vm) => {
      console.log('üîÑ Vue Router ÈÄ≤ÂÖ• UserIndexÔºåÈáçÊñ∞ËºâÂÖ• JS...');
      setTimeout(() => {
        vm.reloadAllScripts();
      }, 300);
    });
  },

  methods: {
    reloadAllScripts() {
      setTimeout(() => {
        console.log('üîÑ Ê™¢Êü• jQuery ÊòØÂê¶Â≠òÂú®...');
        if (typeof window.jQuery === 'undefined') {
          console.warn('‚ö†Ô∏è jQuery is missing, ÈáçÊñ∞ËºâÂÖ• jQuery Âíå Plugins...');
          this.loadScripts([
            `${process.env.BASE_URL}js/jquery.min.js`,
            `${process.env.BASE_URL}js/plugins.js`,
            `${process.env.BASE_URL}js/script.min.js`,
          ]);
        } else {
          console.log('‚úÖ jQuery Â∑≤ËºâÂÖ•ÔºåËºâÂÖ• Plugins Âíå Script...');
          this.loadScripts([
            `${process.env.BASE_URL}js/plugins.js`,
            `${process.env.BASE_URL}js/script.min.js`,
          ]);
        }
      }, 300);
    },
    loadScripts(scripts) {
      return scripts.reduce((promise, src) => promise.then(() => new Promise((resolve, reject) => {
        console.log(`üì• ËºâÂÖ• JS: ${src}`);
        const script = document.createElement('script');
        script.src = src;
        script.async = false; // Á¢∫‰øùÊåâÈ†ÜÂ∫èËºâÂÖ•
        script.onload = resolve;
        script.onerror = reject;
        document.body.appendChild(script);
      })), Promise.resolve());
    },

    // ËºâÂÖ• Áî¢ÂìÅ
    getProducts() {
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/products/all`;
      this.isLoading = true;
      this.$http.get(url).then((response) => {
        this.products = response.data.products;
        console.log('products:', response);
        this.isLoading = false;
      });
    },
    getProduct(id) {
      this.$router.push(`/product/${id}`);
    },
    addCart(id) {
      this.isLoading = true;
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/cart`;
      this.status.loadingItem = id; // Áï∂ÂÆÉÁ≠âÊñºid  btn addcart disable
      const cart = {
        product_id: id,
        qty: 1,
      };
      this.$http.post(url, { data: cart })
        .then((res) => {
          this.status.loadingItem = ''; // ajaxËºâÂÖ•‰πãÂæå ÂÜçÊääidÊ∏ÖÁ©∫
          console.log(res);
          this.$httpMessageState(res, 'Âä†ÂÖ•Ë≥ºÁâ©Ëªä');
          this.getCart();
          this.isLoading = false;
          this.$emit('update-cart'); // ÂëäË®¥Áà∂ÂÖÉ‰ª∂Ë¶ÅÊõ¥Êñ∞
        });
    },
    getCart() {
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/cart`;
      this.$http.get(url).then((response) => {
        console.log(response);
        this.cart = response.data.data;
      });
    },

  },
  created() {
    this.getProducts();
    this.getCart();
  },

};
</script>
